@import url('https://fonts.googleapis.com/css2?family=Bitter&family=Raleway:wght@300&display=swap');

*{
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  background: aliceblue;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(#fcf5f5 , #eff4fd);
}

.main_div {
  width: 40vw;
  padding: 50px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px,
  rgba(17,17,26,0.1) 0px 0px 8px;
}

h1{
  text-align: center;
  font-family: "Bitter", serif;
  text-transform: capitalize;
  font-size: 32px;
  letter-spacing: 5;
  word-spacing: 5px;
  margin-bottom: 50px;
  color: #2c3148;
}

.main_heading {
  display: flex;
  /* flex-wrap: nowrap; */
  margin-top: 30px;
  padding: 20px 0 20px 20px;
  background: #f1f4ff;
  border-radius: 5px;
  
}

.main_heading h3{
  margin-left: 30px;
  /* font-family: "Mate SC", serif; */
  color: #303243;
  font-family: "Raleway", sans-serif;
  word-spacing: 1px;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 18px;
}

.main_heading p {
  cursor: pointer;
  border-radius: 50%;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
      rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
      background: #fff;
      padding: 5px;
}

.answers {
  padding: 30px 20px 40px 20px;
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  word-spacing: 2px;
  font-weight: 400;
  line-height: 1.7;
  background: #fcf5f5;
  color: rgb(40, 37, 53);
}